* {
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-weight: 200;
    font-style: normal;
    font-variation-settings: "slnt" 0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    background-color: #212428;
    color: #f3f4f4;
}

.background-card,
.display-pic-bg {
    background: #212427;
    background: linear-gradient(45deg,#212427 0%, #23272b 100%);
    background: -webkit-linear-gradient(135deg,#212427 0%, #23272b 100%);
    background: -moz-linear-gradient(135deg,#212427 0%, #23272b 100%);
    color: rgb(135,142,153);
    box-shadow: 5px 5px 30px -10px rgba(0,0,0,1), -5px -5px 15px -10px rgba(135,142,153);
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}

.background-card:hover {
    background: rgb(33,36,40);
    background: -moz-linear-gradient(135deg, rgba(33,36,40,1) 0%, rgba(22,24,28,1) 100%);
    background: -webkit-linear-gradient(135deg, rgba(33,36,40,1) 0%, rgba(22,24,28,1) 100%);
    background: linear-gradient(135deg, rgba(33,36,40,1) 0%, rgba(22,24,28,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#212428",endColorstr="#16181c",GradientType=1);
    color: #f3f4f4;
}

.background-card:not(:hover) {
    background: #212427;
    background: linear-gradient(135deg,#212427 0%, #23272b 100%);
    background: -webkit-linear-gradient(135deg,#212427 0%, #23272b 100%);
    background: -moz-linear-gradient(135deg,#212427 0%, #23272b 100%);
    color: rgb(135,142,153);
}

