#about {
    /* display: flex; */
    justify-content: center;
    padding: 7.5% 7.5% 2%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding-bottom: 10%;
}

.about-me {
    color: #C4CFDE;
    font-size: 4rem;
    transition: color 0.3s ease; /* Define transition for color change */
    text-align: center;
}

.about-me:hover {
    color: #FF014F; /* Change color on hover */
    transition: color 0.5s ease-in-out; /* Define transition for color change on hover */
}

.about-me:not(:hover) {
    color: #C4CFDE; /* Revert color to original when not hovered */
    transition: color 0s ease; /* Define transition for color change when not hovered */
}

.about-me-container {
    display: flex;
    justify-content: center;
    padding: 2% 0;
}

.about-me-text {
    color: #f3f4f4;
    font-size: 1.3rem;
    text-align: justify;
}

.about-image {
    margin: 0 4%;
    width: auto;
    height: 30rem;
}
